html {
  height: 100%;
}

.apple {
  --font-display: -apple-system, "SF Pro Text", "SF UI Text", system-ui,
    "Helvetica Neue", Helvetica, Arial, sans-serif;
  --font-display-send: ui-rounded, sans-serif;
  // --font-display-title: -apple-system, serif;
  // --font-display: ui-rounded, sans-serif;
}

.material {
  --font-display: -apple-system, "SF Pro Text", "SF UI Text", system-ui,
    "Helvetica Neue", Helvetica, Arial, sans-serif;
  --font-display-send: Roboto, sans-serif;
  // --font-display-title: Roboto, serif;
  // --font-display: Roboto,system-ui,sans-serif;
}

* {
  box-sizing: border-box;
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  font-family: var(--font-display), sans-serif;
  font-weight: 400;
  color: var(--color-text-primary);
  background-color: #000;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  margin: 0;
  -webkit-overflow-scrolling: touch !important;
  user-select: none;
  // outline: 0;
}

.scrolling {
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-touch-callout: none;
  user-select: none;
}

.container {
  // padding: 0 14px;
  margin: 0 auto;
  max-width: 100%;
}

.position-relative {
  position: relative;
}

.page {
  position: relative;
  &__content {
    min-height: 100vh;
    // padding: 16px 0;
  }
}

button {
  cursor: pointer;
  border: none;
  background: transparent;
  font-family: var(--font-display), sans-serif;
}

ul {
  padding: 0;
}

a {
  -webkit-user-drag: none;
  text-decoration: none;
}

.background-influencer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("./assets/images/bg-influencer.png") no-repeat center
    center/cover #000;
  will-change: background-position;
  transition: cubic-bezier(0.7, 0.55, 0.42, 0.33) 0.15s background-position;
  filter: blur(5px);
  -webkit-filter: blur(5px);
}

.background-yellow {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: 100%;
  background-position: center top;
  background-repeat: no-repeat;
  transition: opacity 0.35s ease;
  filter: blur(5px);
  // -webkit-filter: blur(5px);
}

.theme-yellow {
  background-image: url("./assets/images/bg-yellow.png");
  background-position: center bottom;
}

.background-compete {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: 100%;
  background-position: center top;
  background-repeat: no-repeat;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  // transform: rotate(180deg);
}

.theme-compete {
  // background-image: url("./assets/images/soon_bg.jpeg");
  background-image: url("./assets/images/bg-influencer.png");
}

.balance-icon {
  margin-right: 12px;
  width: 40px;
  height: 40px;
  background-image: url("./assets/images/skin-galleon-back.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-color: transparent;
  margin-top: 2px;
}

.penny-icon {
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("./assets/images/penny.png");
}

.icon-coin-small {
  background-image: url("./assets/images/icon-coin-small.png");
  background-repeat: no-repeat;
  background-size: contain;
}

.score-icon {
  background-repeat: no-repeat;
  background-size: contain;
  background-color: transparent;
  background-image: url("./assets/images/coin-min.svg");
}

.rocket {
  position: absolute;
  height: 80px;
  width: 80px;
  user-select: none;
  z-index: 10;
  &_child-1 {
    top: 0;
    left: 0;
