// SWIPER

.swiper {
  position: relative;
  overflow: initial !important;
}

.nav-btn {
  position: absolute;
  width: 36px;
  height: 36px;
  transition: background-color ease;
  transition-duration: 0.3s;
  border-radius: 8px;
  &_prev {
    left: 0px;
    transform: translateY(-50%) scaleX(-1);
    transform-origin: center;
    &:active {
      transition-duration: 0s;
      background-color: rgba(235, 235, 245, 0.1);
      border-radius: 8px;
    }
  }
  &_prev-disable {
    left: 0px;
    transform: translateY(-50%) scaleX(-1);
    transform-origin: center;
    cursor: auto;
  }
  &_next {
    right: 0px;
    transform: translateY(-50%);
    &:active {
      transition-duration: 0s;
      background-color: rgba(235, 235, 245, 0.1);
      border-radius: 8px;
    }
  }
  &_next-disable {
    right: 0px;
    transform: translateY(-50%);
    cursor: auto;
  }
}

.mzQ1 {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 24px 12px;
  border-radius: 14px;
  background: rgba(255, 255, 255, 0.12);
  margin-top: 12px;
}

.LibrarySegmentedControl {
  background-color: rgba(255, 255, 255, 0.12);
  border-radius: 14px;
  box-sizing: border-box;
  display: inline-block;
  margin-top: 12px;
  padding: 4px;
  position: relative;
}
.LibrarySegmentedControl--glider .LibrarySegmentedControlItem--active {
  background: transparent;
}
.LibrarySegmentedControl--state-fixed {
  display: flex;
  gap: 4px;
}
.LibrarySegmentedControl--state-fixed .LibrarySegmentedControlItem {
  box-sizing: border-box;
  justify-content: center;
  margin: 0;
  width: 100%;
}
.LibrarySegmentedControl--state-fixed .LibrarySegmentedControlItem__link {
  flex-basis: 100%;
  min-width: 0;
}
.LibrarySegmentedControl__glider {
  background-color: #fff;
  border-radius: 10px;
  box-sizing: border-box;
  display: block;
  height: calc(100% - 8px);
  left: 4px;
  position: absolute;
  top: 4px;
  transition: 0.25s ease-out;
}
.LibrarySegmentedControlItem {
  border-radius: 10px;
  display: inline-flex;
  margin: 0 4px;
  padding: 9px 20px;
}
.LibrarySegmentedControlItem--active {
  background-color: #fff;
}
.LibrarySegmentedControlItem--active .LibrarySegmentedControlItem__inner {
  color: #000 !important;
  font-size: 17px;
  font-weight: 600;
}
@media (hover: hover), (min-width: 768px) {
  .LibrarySegmentedControlItem {
    transition: background-color 0.2s ease-in-out;
  }
  .LibrarySegmentedControlItem:hover:not(.LibrarySegmentedControlItem--active) {
    cursor: pointer;
  }
}
.LibrarySegmentedControlItem__inner {
  color: #fff !important;
  font-size: 17px;
  font-weight: 600;
}
.LibrarySegmentedControlItem__counter {
  margin-left: 8px;
}
.LibrarySegmentedControlItem__link {
  position: relative;
  z-index: 1;
}

