        url("../../assets/images/turbo8-ticker.png");
      background-size: contain;
    }
  }
  
  @keyframes ticker-animation {
    0% {
      background-position: 0 0, 0 50vh;
    }
    100% {
      background-position: 0 50vh, 0 100vh;
    }
  }
  
  .shadow-string {
